<template>
  <div class="bidding">
    <Crumbs
      :parent-manage="'信息管理'"
      :children-manage="'信息发布'"
      @isShowScreen="isShowScreen"
    ></Crumbs>

    <!-- 筛选内容 -->
    <Content
      v-if="isScreen"
      class="content screenStyle"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="searchData"
        :label-width="85"
      >
        <Row>
          <Col span="11">
          <FormItem label="卷名" width="100%">
            <Select
              v-model="searchData.juanming"
              transfer
              filterable
              clearable
            >
              <Option v-for="item in volumeList" :key="item.adminMenuId" :value="item.adminMenuId">{{ item.adminMenuName }}</Option>
            </Select>
          </FormItem>

          </Col>
          <Col span="11" offset="2">
          <FormItem label="案卷题名" width="100%">
            <Select
              v-model="searchData.anjuan_timin"
              transfer
              filterable
              clearable
            >
              <Option v-for="item in titleList" :key="item.adminMenuId" :value="item.adminMenuId">{{ item.adminMenuName }}</Option>
            </Select>
          </FormItem>
          </Col>
        </Row>
        <!-- 提交开始和结束 -->
        <Row style="margin-top:15px;">
          <Col span="11">
          <FormItem label="日期开始时间">
            <DatePicker
              v-model="searchData.startTime"
              style="width:100%;"
              type="date"
              :options="startTimeOptions"
              placeholder="请选择日期开始时间"
              placement="bottom-start"
              @on-change="startTimeChange"
            ></DatePicker>
          </FormItem>
          </Col>
          <Col span="11" offset="2">
          <FormItem label="日期结束时间">
            <DatePicker
              v-model="searchData.endTime"
              style="width:100%;"
              type="date"
              :options="endTimeOptions"
              placeholder="请选择日期结束时间"
              placement="bottom-start"
              @on-change="endTimeChange"
            ></DatePicker>
          </FormItem>
          </Col>
        </Row>
        <Row style="margin-top:15px;text-align:right">
          <Button type="primary" @click="searchList">确认</Button>
        </Row>
      </Form>
    </Content>
    <!-- table-content -->
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button
        type="primary"
        icon="md-add"
        @click="handleAdd"
      >
        新增
      </Button>
      <Button
        type="primary"
        icon="ios-create"
        @click="handleModify"
      >
        修改
      </Button>
      <Button
        type="primary"
        icon="md-close"
        @click="handleDelete"
      >
        删除
      </Button>
      <Button icon="md-print" @click.stop="printContent(18)">
        打印
      </Button>
      <Button icon="md-exit" @click="excelExcel('/archives/exportArchivesListExcel')">
        导出
      </Button>
      <Table
        ref="archivesList"
        highlight-row
        class="table"
        :columns="columns"
        :data="archivesList"
        style="margin-top: 15px;"
        @on-current-change="selectRow"
      ></Table>
      <!-- 分页组件 -->
      <Page
        style="text-align: right"
        :total="total"
        :current="searchData.pageNo"
        :page-size="searchData.pageSize"
        show-sizer
        transfer
        @on-change="pageNoChange"
        @on-page-size-change="pageSizeChange"
      />
      <!-- 弹出框 -->
      <Modal
        v-model="popUp"
        class="selStyle specialWidth"
        :mask-closable="false"
      >
        <p slot="header">
          <span v-if="!isDisabled">请添加档案信息</span>
          <span v-else>档案信息</span>
        </p>
        <Tabs
          :animated="false"
          class="innerTabStyle"
          value="name1"
        >
          <TabPane
            label="信息设置"
            name="name1"
          >
            <Form
              ref="form"
              :model="form"
              :rules="ruleValidateType"
              :label-width="80"
            >
              <Row>
                <Col span="24">
                <FormItem
                  label="标题"
                  prop="name"
                >
                  <Input
                    v-model.trim="form.name"
                    :disabled="isDisabled"
                    clearable
                    placeholder="请填写标题"
                  ></Input>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="24">
                <FormItem
                  label="发布人"
                  prop="juan_name"
                >
                  <Input
                    v-model.trim="form.name"
                    :disabled="isDisabled"
                    clearable
                    placeholder="请输入发布人"
                  ></Input>
                </FormItem>
                </Col>
                <Col span="12">
                <FormItem
                  label="资金类别"
                  prop="anjuan_timin"
                >
                  <Select
                    v-model="form.anjuan_timin"
                    :disabled="isDisabled"
                    transfer
                    filterable
                    clearable
                    placeholder="请选择资金类别"
                  >
                    <Option v-for="item in archiveTitleList" :key="item.adminMenuId" :value="parseInt(item.adminMenuId)">{{ item.adminMenuName }}</Option>
                  </Select>
                </FormItem>
                </Col>
              </Row>
              <Col span="24">
              <FormItem label="备注" prop="remark">
                <Input
                  v-model.trim="form.remark"
                  :disabled="isDisabled"
                  type="textarea"
                  :autosize="{minRows: 1,maxRows: 5}"
                  placeholder="请填写备注"
                ></Input>
              </FormItem>
              </Col>
            </Form>
          </TabPane>
          <TabPane
            label="附件"
            name="name7"
          >
            <uploadFile ref="uploadFile" :is-disabled="isDisabled" :form="{fileids: form.archivesFileids}" />
          </TabPane>
        </Tabs>
        <p
          slot="footer"
          style="text-align: left"
        >
          <Button icon="md-print">打印</Button>
          <Button icon="md-exit">导出</Button>
          <Button
            v-if="!isDisabled"
            style="float: right;margin-left: 8px"
            @click="handleCancel('form')"
          >
            取消
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right"
            type="primary"
            @click="handleConfirm('form')"
          >
            确认
          </Button>
        </p>
      </Modal>
      <!-- /弹出框 -->
    </Content>
    <!--审核的弹出框-->
    <audit ref="audit" :form="auditForm" />
  </div>
</template>

<script>
import Crumbs from '../components/Crumbs';
import uploadFile from '@/components/fileUpload.vue'
import tablePage from '@/mixins/tablePage.js';
import audit from '@/components/audit.vue'
import JsBarcode from 'jsbarcode'
export default {
  name: 'Bidding',
  components: { Crumbs, uploadFile, audit },
  mixins: [tablePage],
  data() {
    return {
      // 控制筛选框显示隐藏  默认隐藏
      isScreen: false,
      // 筛选内容_字段
      searchData: {
        // 案卷题名
        anjuan_timin: '',
        // 时间日期
        startTime: null,
        endTime: null,
        juanming: '',
        ascending: false,
        orderBy: '',
        pageNo: 1,
        pageSize: 10
      },
      // 表格内容 字段相对应
      archivesList: [],
      // 弹出框内容显示隐藏   默认隐藏
      popUp: false,
      // 添加表单的内容字段  ->  传递数据的字段
      form: {
        anjuan_timin: '',
        juan_name: '',
        fileTitle: '',
        name: '',
        remark: ''
      },
      // 表单内容的校验规则
      ruleValidateType: {
        name: [
          { required: true, message: '请填写文件名称', trigger: 'blur' }
        ],
        juan_name: [
          { type: 'number', required: true, message: '请选择卷名', trigger: 'change' }
        ],
        anjuan_timin: [
          { type: 'number', required: true, message: '请选择案卷题名', trigger: 'change' }
        ],
        remark: [
          { required: true, message: '请填写备注', trigger: 'blur' }
        ]
      },
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          fixed: 'left',
          indexMethod: this.indexMethod
        },
        {
          title: '发布时间',
          key: 'juan_name',
          align: 'center',
          render: (h, params) => {
            const id = params.row.juan_name
            const juan_name = this.volumeList.filter(item => {
              return item.adminMenuId === id;
            })[0].adminMenuName.replace(/管理/g, '文件')
            return h('div', juan_name)
          }
        },
        {
          title: '标题',
          key: 'anjuan_timin',
          align: 'center',
          render: (h, params) => {
            const id = params.row.anjuan_timin
            const anjuan_timin = this.titleList.filter(item => {
              return item.adminMenuId === id;
            })[0].adminMenuName
            return h('div', anjuan_timin)
          }
        },
        {
          title: '发布人',
          key: 'name',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.showInfo(params.row, 'popUp')
                }
              }
            }, params.row.name);
          }
        },
        {
          title: '资金类别',
          key: 'capitalName'
        }
      ],
      listUrl: '/archives/searchArchives/list',
      volumeList: [],
      titleList: [],
      archiveTitleList: []
    }
  },
  created() {
    this.searchList()
    this.getSelectList()
  },
  methods: {
    // 删除
    handleDelete() {
      this.deleteData('/archives/deleteArchives/', 'id')
    },
    // 卷名
    getSelectList() {
      const menuList = this.$store.state.user.menuList
      const volumeList = []// 卷名
      const titleList = []// 案卷题名
      for (const item of menuList) {
        if (item.isVolumeName === 1) {
          const menuModelList = item.menuModelList
          item.adminMenuName = item.adminMenuName.replace(/管理/g, '文件')
          volumeList.push(item)
          if (menuModelList.length > 0) {
            titleList.push.apply(titleList, menuModelList);
          }
        }
      }
      this.volumeList = volumeList;
      this.titleList = titleList;
      sessionStorage.setItem('volumeList', JSON.stringify(volumeList))
      sessionStorage.setItem('titleList', JSON.stringify(titleList))
      this.archiveTitleList = titleList;
    },
    // 卷名变化，题名也要改变
    changeVolume(id, flag) {
      const volumeList = this.volumeList
      if (!flag) {
        this.form.anjuan_timin = null
      }
      if (id) {
        this.archiveTitleList = volumeList.filter(item => {
          return item.adminMenuId === id;
        })[0].menuModelList
      } else {
        this.archiveTitleList = this.titleList
      }
    },
    searchList() {
      const that = this;
      that.search(that.getNewSearchData(), (flag, res) => {
        if (flag) {
          const data = res.data
          that.archivesList = data.archivesList
          that.total = data.totalCount
          that.$nextTick(() => {
            // 生成条形码
            for (const item of data.archivesList) {
              JsBarcode('#code' + item.id, item.code, {
                format: 'CODE128', //  选择要使用的条形码类型
                text: item.code,
                displayValue: true, // 是否在条形码下方显示文字
                textPosition: 'bottom'// 设置文本的垂直位置
              })
            }
          })
        }
      })
    },
    // 子组件接收 控制筛选内容的隐藏和显示
    isShowScreen(msg) {
      this.isScreen = msg;
    },
    // 添加批复 弹出框显示
    handleAdd() {
      this.clearData();
      this.isCreate = true
      // 禁用状态关闭
      this.isDisabled = false;
      // 弹出框显示
      this.popUp = true;
    },
    clearData() {
      this.form = {
        anjuan_timin: '',
        juan_name: '',
        fileTitle: '',
        name: '',
        remark: ''
      }
      this.$refs.form.resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    },
    // 更新操作
    handleModify() {
      if (!this.isSelected) {
        this.$Message.warning('您未选择需要修改的内容');
        return
      }
      this.clearData()
      const form = Object.assign({}, this.currentRow)
      this.form = form
      this.changeVolume(form.juan_name, true)
      this.isCreate = false
      // 禁用状态关闭
      this.isDisabled = false;
      this.$nextTick(() => {
        this.$refs.uploadFile.getFileItems()
        this.popUp = true;
      })
    },
    // 取消操作
    handleCancel(name) {
      this.$refs[name].resetFields();
      this.popUp = false;
    },
    // 确认操作,关系后台数据
    handleConfirm(name) {
      const isCreate = this.isCreate
      const form = Object.assign({}, this.form)
      const method = isCreate ? '/archives/addArchives' : '/archives/updateArchives'
      form.fileIds = this.$refs.uploadFile.getFileIds()
      this.submitForm(name, form, method, () => {
        this.popUp = false;
        this.searchList()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  .upload_text {
    font-weight: 800;
    margin-left: 20px;
    display: inline-block;
    width: 120px;
  }
</style>



